import React, { useState, useEffect } from "react";
import SmartSlider from "react-smart-slider";
import slider from 'react-slick'
const Events = ({ storedTheme, setStoredTheme, ...rest }) => {
  const { t } = useTranslation();

  const languageMap = {
    en: { label: "English", dir: "ltr", active: true },
    gr: { label: "Franais", dir: "ltr", active: false },
  };

  const [selectedLang, setSelectedLang] = useState(
    localStorage.getItem("i18nextLng") || "en"
  );
  const setDark = () => {
    setStoredTheme("dark");
    localStorage.setItem("theme", "dark");
    //document.documentElement.setAttribute("data-theme", "dark");
  };

  const setLight = () => {
    setStoredTheme("light");
    localStorage.setItem("theme", "light");
    //document.documentElement.setAttribute("data-theme", "light");
  };

  const toggleTheme = (e) => {
    if (storedTheme === "light") {
      setDark();
    } else {
      setLight();
    }
  };

  useEffect(() => {
    localStorage.setItem("theme", storedTheme);
  }, []);


   let settings ={
        
    Infinite:true,
    speed:700,
    slidesToShow:1,
    slidesToScroll:1,
    autoplay:true
}
  return(
    
  <section id='events' className={storedTheme} style={
    storedTheme === "dark"
      ? { backgroundColor: "#111111" }
      : { backgroundColor: "#e9e8e8" }
  }>
  <section className='events' style={
    storedTheme === "dark"
      ? { backgroundColor: "#111111" }
      : { backgroundColor: "#e9e8e8" }
  }>
    <Fade bottom>
    
    <div class={storedTheme} style={
    storedTheme === "dark"
      ? { backgroundColor: "#111111" }
      : { backgroundColor: "#e9e8e8" }
  }>
		<h2>{t("Event")}</h2>
		<h2>{t("Event")}</h2>
	 </div>
   </Fade>
  </section>

   <Container >
  
   <Carousel {... settings}>
    <Wrap>
        <img src='/images/ggj.jpg' alt=''/>
    </Wrap>
    <Wrap>
        <img src='/images/python.jpg' alt=''/>
    </Wrap>
    <Wrap>
        <img src='/images/c++.jpg' alt=''/>
    </Wrap>
    <Wrap>
        <img src='/images/uiux.jpg' alt=''/>
    </Wrap>
   
    </Carousel>


 </Container>
 </section>
  )
}


