import React from 'react'
import "./Hero.css"
import { useState, useEffect } from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import headerImg from "../assets/images/logo.png";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import Zoom from 'react-reveal/Zoom';
import { useTranslation } from "react-i18next";
import i18next from "i18next";
import Fade from 'react-reveal/Fade';
import Roll from 'react-reveal/Roll';
import Bounce from 'react-reveal/Bounce';
const Bannere5er=({ storedTheme, setStoredTheme, ...rest })=> {

  const { t } = useTranslation();

  const languageMap = {
    en: { label: "English", dir: "ltr", active: true },
    gr: { label: "Franais", dir: "ltr", active: false },
  };

  const [selectedLang, setSelectedLang] = useState(
    localStorage.getItem("i18nextLng") || "en"
  );

  const setDark = () => {
    setStoredTheme("dark");
    localStorage.setItem("theme", "dark");
    //document.documentElement.setAttribute("data-theme", "dark");
  };

  const setLight = () => {
    setStoredTheme("light");
    localStorage.setItem("theme", "light");
    //document.documentElement.setAttribute("data-theme", "light");
  };

  const toggleTheme = (e) => {
    if (storedTheme === "light") {
      setDark();
    } else {
      setLight();
    }
  };

  useEffect(() => {
    localStorage.setItem("theme", storedTheme);
  }, []);

  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  const b= t("3d_Model");
  
  const toRotate = [ b];
  
  const period = 1300;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 2);
      setIndex(1);
      setDelta(200);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }



  return (
    
    <div className='banner_home'>
      
        <div class="item item-b">
         <div className='banner_home_text1'>
         <Fade  right cascade>
        
         <h1 className="lynxcoder1">{t("with")} </h1>
         
         <h1> <span className="lynxcoder">LynxCoder</span> <span className="lynxcoder2">{t("learn")}</span><br/> <span className='lynxcoder3'>{t("build_your_own")} </span>  <span className="txt-rotate" dataPeriod="2000"  data-rotate='[ "3D Model","solve","others"]'><span className="wrap">{text}</span></span></h1>
        </Fade >
        </div>
       </div>  
        
       <div class="item item-c">
       <div className='banner_home_text2'>   
         <img src={headerImg} alt="Header Img"  style={{ cursor: "pointer",width: "30rem"}}/>
        </div>
        </div>
    </div>
  )
}

export default Bannere5er
